:root {
  --color-background: #f4f5f9;
  --color-accent: #e54065;
  --color-border: #cfd2dc;
  --color-text: #636363;
  --color-filter: #e1e4ea;
  --color-read-background: #f2f2f2;
}

body {
  background-color: var(--color-background);
  font-family: "Roboto", sans-serif;
  font-size: 14px;
}

.bold {
  font-weight: bold;
}
